import React from "react";
import input from "../css/inputTask.css";
import ReactDom from "react-dom";
import Task from "../Task.js";
import Heading from "../components/Heading";
import DisplayTask from "../components/DisplayTask";

var count = 0;
function AddTask(){
    var task = document.getElementById("enteredTask");
    var a  = "item"+(count++);
    var b = { [a] : task.value};
    Task.push(b); 
    document.getElementById("enteredTask").value = "";
    ReactDom.render(
        <>
            <Heading />
            <InputTask />
            <DisplayTask />
        </>, document.getElementById("root")
    );
}

function InputTask(){
    return <><div className="inputBox">
        <input type="text" name="task" placeholder="Enter your task" id="enteredTask" />
        <button type="submit" id="addButtom" onClick={AddTask}> Add Task</button>
    </div>
    </>
}

export default InputTask;