import React from "react";
import Task from "../Task";
import displayTask from "../css/displayTask.css";

function returnListItem(item){    

    var id = Object.keys(item)[0]
    return <h3 id ={id} key= {id} onClick={() =>
    {
        if(document.getElementById(id).style.textDecoration.toString()===""){
            document.getElementById(id).style.textDecoration = "line-through";
            document.getElementById(id).style.color="red";
        }
        else{
            document.getElementById(id).style.textDecoration = null;
            document.getElementById(id).style.color="black";
        }        
    }}> {item[id]}</h3>
}

function DisplayTask(){
    return(        
        <div className="displayTask">
            {Task.map(returnListItem) }
        </div>
    );
}



export default DisplayTask;